html {}
body {
  font-family: 'Open Sans', sans-serif;
  font-size: 18px;
  box-sizing: border-box;
  position: relative; }
.container {
  width: 100% !important;
  max-width: 1165px;
  box-sizing: border-box;
  @include r(1200) {
    padding: 0 15px; } }
a {
  text-decoration: none;
  transition: all 0.3s; }
input[type="submit"] {
  appearance: none;
  -webkit-appearance: none; }
textarea {
  resize: none; }


@include r(767) {
	.hidden-xs {
		display: none !important; } }
@media only screen and (min-width: 768px) and (max-width: 1024px) {
	.hidden-sm {
		display: none !important; } }
@include rmin(1024) {
	.hidden-md {
		display: none !important; } }


